.App {
    text-align: center;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
    background-size: cover;
    background-attachment: center;
    background-position: center;
}
.header {
    background: rgba(0, 0, 0, 0.62);
    padding: 10px;
    color: white;
}
    .header h1 {
        margin-top: 0;
    }

.github{
    float: right;
    position: absolute;
    top: 15px;
    right: 25px;
}
    .github img{
        height:60px;
    }

.container {
    resize: both;
    width: calc(100vw - 4px);
    overflow: auto;
    border: 2px solid white;
        margin-left: auto;
    margin-right: auto;
    margin-top:10px;
}

.resize{
    color:white;
    background:rgba(0, 0, 0, 0.62);
}
.box {
    height: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
}
